body {
  margin: 60px;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 20px;
}

button {
  width: 75px;
  margin: 5px;
  padding: 7px 5px;
  background-color: rgb(228, 227, 227);
  border: none;
  border-radius: 8px;
  text-align: center;
  font-size: 16px;
  &:focus,
  &:hover {
    color: rgb(228, 227, 227);
    background-color: blue;
    border-radius: 2px solid grey;
  }
}

li {
  list-style-type: none;
}
